import React, { useState } from "react";
import Chart from "react-apexcharts";

import ShowRegressionString from "./ShowRegressionString";

import Container from "react-bootstrap/Container";
import ButtonGroup from "react-bootstrap/ButtonGroup";
import Button from "react-bootstrap/Button";

function ScatterChartBetaCompany(props) {
    const [company, setCompany] = useState("ROSN");

    let companies = [
        "SBER",
        "YNDX",
        "VTBR",
        "SNGSP",
        "SBERP",
        "ROSN",
        "NLMK",
        "MTSS",
        "MGNT",
        "MFON",
        "GMKN",
        "GAZP",
        "AFLT",
        "IMOEX"
    ];


    const round = (value, decimals) => {
        return Number(Math.round(value + "e" + decimals) + "e-" + decimals);
    };

    let imoex = [];
    let selectedstock = [];
    let line1 = [];
    let line2 = [];

    props.data.forEach(item => {
        if (item.node.TICKER === "IMOEX") {
            let result = round(
                ((item.node.CLOSE - item.node.OPEN) * 100) / item.node.OPEN,
                2
            );
            if (!isNaN(result)) {
                imoex.push({
                    DATE: item.node.DATE,
                    PERIODRETURN: result
                });
            }

            line1.push({ date: item.node.DATE, value: parseFloat(item.node.CLOSE) });

        }

        if (item.node.TICKER === company) {
            let result = round(
                ((item.node.CLOSE - item.node.OPEN) * 100) / item.node.OPEN,
                2
            );
            if (!isNaN(result)) {
                selectedstock.push({
                    DATE: item.node.DATE,
                    PERIODRETURN: result
                });
            }

            line2.push({ date: item.node.DATE, value: parseFloat(item.node.CLOSE) });
        }



    });

    // console.table(line1);
    // console.table(line2);

    //  console.log(line2[0].date);
    let minLine2 = line2[0].value;
    let minLine1 = line1.filter(item => item.date === line2[0].date)[0].value;

    let newline1 = line1.map((item) => {
        let newItem = { ...item };
        newItem.value = item.value * 100 / minLine1;
        return newItem
    });
    let newline2 = line2.map((item) => {
        let newItem = { ...item };
        newItem.value = item.value * 100 / minLine2;
        return newItem
    });

    //  console.table(newline1);
    //  console.table(newline2);


    let dataforlinechart = {
        line1: newline1,
        line2: newline2
    };;

    // if (line1.length !== 0 && line2.length !== 0) {
    //   dataforlinechart = {
    //     line1: line1,
    //     line2: line2
    //   };
    // }

    // let dataforlinechart = {
    //   line1: [
    //     { date: "19970901", value: 100 },
    //     { date: "19980901", value: 270 },
    //     { date: "19990901", value: 2000 },
    //     { date: "20000901", value: 1400 },
    //     { date: "20050901", value: 1500 },
    //     { date: "20120901", value: 2100 },
    //     { date: "20190901", value: 2900 }
    //   ],
    //   line2: [
    //     { date: "19970901", value: 200 },
    //     { date: "19980901", value: 170 },
    //     { date: "19990901", value: 2200 },
    //     { date: "20000901", value: 1100 },
    //     { date: "20050901", value: 1800 },
    //     { date: "20120901", value: 2700 },
    //     { date: "20190901", value: 2900 }
    //   ]
    // };


    let scatterdata = [];
    let arrayOfarrays = []; //["Date", "IMOEX", company]


    selectedstock.length > 0 &&
        selectedstock.forEach(item => {
            let found = imoex.find(function (element) {
                return element.DATE === item.DATE;
            });
            let imoexPeriodReturn = found.PERIODRETURN;

            scatterdata.push({
                horizontal: imoexPeriodReturn,
                vertical: item.PERIODRETURNx
            });
            arrayOfarrays.push([imoexPeriodReturn, item.PERIODRETURN]); //item.DATE,
        });

    //    console.log(props);

    let areaoptions = {
        chart: {
            type: 'area',
            toolbar: {
                show: false,
            }
        },
        dataLabels: {
            enabled: false
        },
        stroke: {
            curve: 'smooth'
        },
        xaxis: {
            type: 'datetime',
            categories: [...new Array(50)].map((item, index) => { var d = new Date(); return d.setMonth(d.getMonth() - 50 + index) })
        },
        tooltip: {
            x: {
                format: 'dd/MM/yy'
            },
        },
    };

    let scatteroptions = {
        chart: {
            // height: 350,
            type: 'scatter',
            toolbar: {
                show: false,
            },
            zoom: {
                enabled: true,
                type: 'xy'
            }
        },
        xaxis: {
            title: {
                text: 'Доходность фондового индекса за месяц',
                style: {
                    color: "gray",
                    fontSize: '1rem',
                    fontFamily: 'Helvetica, Arial, sans-serif',
                    fontWeight: 200,
                    cssClass: 'apexcharts-yaxis-title',
                }
            },
            tickAmount: 16,
            labels: {
                formatter: function (val) {
                    return parseFloat(val).toFixed(1)
                }
            },
            min: -40,
            max: 40
        },
        yaxis: {
            title: {
                text: 'Доходность актива за месяц',
                style: {
                    color: "gray",
                    fontSize: '1rem',
                    fontFamily: 'Helvetica, Arial, sans-serif',
                    fontWeight: 200,
                    cssClass: 'apexcharts-yaxis-title',
                }
            },
            tickAmount: 16,
            labels: {
                formatter: function (val) {
                    return parseFloat(val).toFixed(1)
                }
            },
            min: -40,
            max: 40
        }
    };


    let scatterseries = [
        {
            name: "Доходность актива за период",
            data: arrayOfarrays
            //    [...new Array(49)].map(() => { let randomReturn = Math.round(Math.random() * 300 - 140) / 10; return [randomReturn, randomReturn * (1 - Math.random())] })
        }
    ];

    let indexData = [100];
    let assetData = [100];
    scatterseries[0].data.map((item, index) => {
        indexData[index + 1] = Math.round(indexData[index] * (1 + item[0] / 100));
        assetData[index + 1] = Math.round(assetData[index] * (1 + item[1] / 100));
        return null
    })

    // console.log(indexData);
    // console.log(assetData);

    let areaseries = [{
        name: 'Индекс',
        data: indexData
    }, {
        name: 'Актив',
        data: assetData
    }];

    //  console.log(dataforlinechart);
 //   console.log(arrayOfarrays);

    return <Container className="mt-3" >
        <ButtonGroup size="sm">
            {companies.map((item, index) => (
                <Button
                    variant="outline-secondary"
                    key={index}
                    onClick={() => setCompany(item)}
                //      style={{ margin: "5px", background: "white" }}
                >
                    {item}
                </Button>
            ))}
        </ButtonGroup>
        <ShowRegressionString arrayOfarrays={arrayOfarrays} />
        <Chart options={scatteroptions} series={scatterseries} type="scatter" />
        {/* <Chart options={areaoptions} series={areaseries} type="area" /> */}
    </Container>

}

export default ScatterChartBetaCompany;
